/**
 *  dom 
 * @param {dom} element dom
 * @example
 * setFullscreen(document.documentElement) // 
 * setFullscreen(document.getElementById("id")) // 
 */
export function setFullScreen(element) {
  if (element.requestFullscreen) {
    element.requestFullscreen();
  } else if (element.mozRequestFullScreen) {
    element.mozRequestFullScreen();
  } else if (element.msRequestFullscreen) {
    element.msRequestFullscreen();
  } else if (element.webkitRequestFullscreen) {
    element.webkitRequestFullScreen();
  }
}

/**
 * 
 * @example
 * exitFullscreen();
 */
export function exitFullScreen() {
  if (!document.fullscreenElement) {
    return;
  }
  if (document.exitFullscreen) {
    document.exitFullscreen();
  } else if (document.msExitFullscreen) {
    document.msExitFullscreen();
  } else if (document.mozCancelFullScreen) {
    document.mozCancelFullScreen();
  } else if (document.webkitExitFullscreen) {
    document.webkitExitFullscreen();
  }
}

/**
 *  window.location.href keyvalue
 * @param {*} key  key
 * @returns window.location.href keyvalue
 * @example
 * const value = getUrlParam(key);
 */
export function getUrlParam(key) {
  const url = window.location.href.replace(/^[^?]*\?/, '');
  const regexp = new RegExp(`(^|&)${key}=([^&#]*)(&|$|)`, 'i');
  const paramMatch = url.match(regexp);

  return paramMatch ? paramMatch[2] : null;
}

/**
 * undefined
 * @param {*} data 
 */
export function isUndefined(data) {
  return typeof data === 'undefined';
}

/**
 * 
 * @param {Function} fn 
 * @param {Number} delay 
 */
export function debounce(fn, delay) {
  let timer = null;
  return function () {
    if (timer) {
      clearTimeout(timer);
    }
    timer = setTimeout(fn, delay);
  };
}

/**
 * 
 */
export function isMobile() {
  return /Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent);
}

/**
 *  Android 
 */
export const isAndroid = /Android/i.test(navigator.userAgent);
export const isMicroMessenger = /MicroMessenger/i.test(navigator.userAgent);
export const isIosSafari = /iPhone/i.test(navigator.userAgent) && /Safari/i.test(navigator.userAgent);

/**
 * 
 * @returns Boolean
 */
export function isVerticalScreen() {
  return window.orientation === 180 || window.orientation === 0;
}

export function parseLocationSearch(search) {
  search = search.replace('?', '');
  const arr = search.split('&');
  return arr.reduce((prev, cur) => {
    // cur: key=value
    const key = cur.split('=')[0];
    const value = cur.split('=')[1];
    return {
      ...prev,
      [key]: decodeURI(value),
    };
  }, {});
}
